<template>
  <div id="app">
    <div id="left-panel">
      <div class="options-container">
        <!-- PayNow Logos -->
        <div
          v-on:click="isPaynowSelected = !isPaynowSelected"
          class="selectable"
          v-bind:class="[isPaynowSelected ? 'selected' : '']"
          style="display: flex; flex-direction: column; align-items: center"
        >
          <img
            v-bind:src="require('./assets/logos/paynow-vertical.jpg')"
            class="payment-logo"
            style="width: 90%; padding: 3%"
          />
          <b>Paynow</b>
        </div>
        <!-- All the other selectable logos -->
        <div v-for="option in options" v-bind:key="option.name">
          <div
            class="selectable option-button"
            v-on:click="toggleOption(option)"
            v-bind:class="[option.checked ? 'selected' : '']"
          >
            <img
              v-bind:src="option.imgSrc"
              class="payment-logo"
              style="width: 80px"
            />
            <div>
              <b>{{ option.name }}</b>
            </div>
          </div>
        </div>
        <!-- Legacy options -->
        <div style="width: 100%; border-top: 1px solid #dddddd; margin: 30px" />
        <div v-for="option in extraOptions" v-bind:key="option.name">
          <div
            class="selectable option-button"
            v-on:click="toggleOption(option)"
            v-bind:class="[option.checked ? 'selected' : '']"
          >
            <img
              v-bind:src="option.imgSrc"
              class="payment-logo"
              style="width: 80px"
            />
            <div>
              <b>{{ option.name }}</b>
            </div>
          </div>
        </div>
      </div>

      <div v-on:click="generatePDF" class="generate-pdf-button">
        <b>Generate PDF</b>
      </div>
    </div>

    <div id="right-panel">
      <!-- START ORIENTATION SELECTION TAB BAR -->
      <div class="tab-bar-container">
        <div
          class="tab-bar-button"
          v-bind:class="
            selectedOrientation == 'vertical' ? 'tab-bar-button-selected' : ''
          "
          v-on:click="selectedOrientation = 'vertical'"
        >
          <div style="height: 20px; width: 10px; background-color: grey" />
        </div>
        <div
          class="tab-bar-button"
          v-bind:class="
            selectedOrientation == 'horizontal' ? 'tab-bar-button-selected' : ''
          "
          v-on:click="selectedOrientation = 'horizontal'"
        >
          <div style="height: 10px; width: 20px; background-color: grey" />
        </div>
        <div
          class="tab-bar-button"
          v-bind:class="
            selectedOrientation == 'sides' ? 'tab-bar-button-selected' : ''
          "
          style="display: flex; justify-content: center"
          v-on:click="selectedOrientation = 'sides'"
        >
          <div style="height: 20px; width: 10px; background-color: grey" />
          <div style="height: 20px; width: 10px" />
          <div style="height: 20px; width: 10px; background-color: grey" />
        </div>
      </div>
      <!-- END ORIENTATION SELECTION TAB BAR -->

      <!-- START POSTER -->
      <!-- Using a container to draw the poster border so the border doesn't get rendered as PDF -->
      <div
        class="poster-container"
        style="border: 1px solid #eeeeee; width: 100%; height: auto"
      >
        <!-- Vertical orientation poster -->
        <div
          v-if="selectedOrientation == 'vertical'"
          id="poster"
          class="poster"
          style="
            position: relative;
            width: 100%;
            padding-top: 141.421356237%; /* Gives an A5 aspect ratio (Portrait mode) */
            background-color: white;
          "
        >
          <div
            class="poster-inner"
            style="
              display: flex;
              flex-direction: column;
              justify-content: flex-end;
              height: 100%;
              width: 100%;
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
            "
          >
            <div
              style="
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                height: 50%;
                padding: 5%;
              "
            >
              <img
                v-if="isPaynowSelected"
                src="@/assets/logos/paynow-vertical.jpg"
                style="width: 100%; height: auto"
              />
              <div
                v-if="selectedOptions.length > 0"
                style="
                  display: flex;
                  flex-direction: row;
                  justify-content: center;
                  align-items: center;
                  flex-wrap: wrap;
                "
              >
                <div
                  v-for="option in selectedOptions"
                  v-bind:key="option.name"
                  style="height: auto; margin: 5px"
                  v-bind:style="verticalOrientationIconDimensions"
                >
                  <img
                    v-bind:src="option.imgSrc"
                    v-if="option.checked"
                    class="payment-logo"
                    style="height: 100%; width: 100%"
                  />
                </div>
              </div>
            </div>
            <div
              style="
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 50%;
                width: 100%;
              "
            >
              <div
                class="hand-container"
                style="
                  display: flex;
                  flex-direction: column;
                  justify-content: flex-end;
                  align-items: center;
                  height: 80%;
                  width: auto;
                "
              >
                <img
                  src="@/assets/logos/hand.png"
                  style="height: 100%; width: auto"
                />
              </div>
              <div class="footer" style="min-height: 20%">
                <div class="footer-left footer-text">
                  <p>Powered by</p>
                  <img src="@/assets/logos/fomo-logo.jpg" class="fomo-logo" />
                </div>
                <div class="footer-right footer-text">
                  +65 6715 8451
                  <br />
                  SGQR@FOMOPAY.COM
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- Horizontal orientation poster -->
        <div
          v-if="selectedOrientation == 'horizontal'"
          id="poster"
          class="poster"
          style="
            position: relative;
            width: 100%;
            padding-top: 70.710678119%; /* Gives an A5 aspect ratio (Landscape mode) */
            background-color: white;
          "
        >
          <div
            class="poster-inner"
            style="
              display: flex;
              flex-direction: column;
              justify-content: flex-end;
              height: 100%;
              width: 100%;
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
            "
          >
            <div
              style="
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: flex-end;
                height: 100%;
                width: 100%;
              "
            >
              <div
                class="hand-container"
                style="
                  display: flex;
                  flex-direction: column;
                  justify-content: flex-end;
                  align-items: center;
                  height: 80%;
                  width: auto;
                "
              >
                <img
                  src="@/assets/logos/hand.png"
                  style="height: 100%; width: auto; max-width: 150px"
                />
              </div>
              <div
                style="
                  display: flex;
                  flex-direction: column;
                  justify-content: space-around;
                  height: 90%;
                  padding: 5px;
                "
              >
                <img
                  v-if="isPaynowSelected"
                  src="@/assets/logos/paynow-horizontal.jpg"
                  style="max-width: 100%; height: auto"
                />
                <div
                  v-if="selectedOptions.length > 0"
                  style="
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    flex-wrap: wrap;
                  "
                >
                  <div
                    v-for="option in selectedOptions"
                    v-bind:key="option.name"
                    style="height: auto; margin: 5px"
                    v-bind:style="horizontalOrientationIconDimensions"
                  >
                    <img
                      v-bind:src="option.imgSrc"
                      v-if="option.checked"
                      class="payment-logo"
                      style="height: 100%; width: 100%"
                    />
                  </div>
                </div>
              </div>
            </div>

            <div class="footer" style="min-height: 12%">
              <div class="footer-left footer-text">
                <p>Powered by</p>
                <img src="@/assets/logos/fomo-logo.jpg" class="fomo-logo" />
              </div>
              <div class="footer-right footer-text">
                +65 6715 8451
                <br />
                SGQR@FOMOPAY.COM
              </div>
            </div>
          </div>
        </div>

        <!-- Sides orientation poster -->
        <div
          v-if="selectedOrientation == 'sides'"
          id="poster"
          class="poster"
          style="
            position: relative;
            width: 100%;
            padding-top: 141.421356237%; /* Gives an A5 aspect ratio (Portrait mode) */
            background-color: white;
          "
        >
          <div
            class="poster-inner"
            style="
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              height: 100%;
              width: 100%;
              position: absolute;
              top: 0;
              left: 0;
              bottom: 0;
              right: 0;
            "
          >
            <div
              v-for="i in [1, 2, 3]"
              v-bind:key="i"
              style="
                display: flex;
                flex: 1;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                height: 100%;
                max-width: 33.3333%;
              "
            >
              <div
                class=""
                style="
                  min-height: 10%;
                  background-color: #d12027;
                  width: 90%;
                  padding: 0% 5%;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  text-align: center;
                  font-weight: 700;
                  color: white;
                "
              >
                <span
                  style="font-size: 40px; font-weight: 900px; line-height: 40px"
                  >SCAN<br />& PAY</span
                >
              </div>
              <div
                style="
                  display: flex;
                  flex-direction: row;
                  justify-content: center;
                  align-items: center;
                  flex-wrap: wrap;
                "
              >
                <div
                  v-for="option in selectedOptions"
                  v-bind:key="option.name"
                  style="height: auto; margin: 5px"
                  v-bind:style="sidesOrientationIconDimensions"
                >
                  <img
                    v-bind:src="option.imgSrc"
                    v-if="option.checked"
                    class="payment-logo"
                    style="height: 100%; width: 100%"
                  />
                </div>
              </div>
              <div
                class=""
                style="
                  min-height: 10%;
                  background-color: #d12027;
                  width: 90%;
                  padding: 0% 5%;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  text-align: center;
                  font-size: 10px;
                  font-weight: 700;
                  color: white;
                "
              >
                <div class="footer-text">Powered by</div>
                <img src="@/assets/logos/fomo-logo.jpg" class="fomo-logo" />
                <div class="footer-text">+65 6715 8451</div>
                <div class="footer-text">SGQR@FOMOPAY.COM</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- END POSTER -->
    </div>
  </div>
</template>

<script>
import jsPDF from "jspdf";
import html2canvas from "html2canvas";

export default {
  name: "app",
  data() {
    return {
      isPaynowSelected: false,
      selectedOrientation: "vertical",
      selectedOptions: [],
      options: [
        {
          name: "Pay Now",
          checked: false,
          imgSrc: require("./assets/logos/paynow.jpg"),
        },
        {
          name: "Shopee Pay",
          checked: false,
          imgSrc: require("./assets/logos/shopee-pay.jpg"),
        },
        {
          name: "Active Wallet",
          checked: false,
          imgSrc: require("./assets/logos/active-wallet.png"),
        },
        {
          name: "Singtel Dash",
          checked: false,
          imgSrc: require("./assets/logos/singtel-dash.png"),
        },
        {
          name: "Ez Link",
          checked: false,
          imgSrc: require("./assets/logos/ezlink.png"),
        },
        {
          name: "Alipay",
          checked: false,
          imgSrc: require("./assets/logos/alipay.png"),
        },
        {
          name: "Union Pay",
          checked: false,
          imgSrc: require("./assets/logos/union-pay.png"),
        },
        {
          name: "WeChat Pay",
          checked: false,
          imgSrc: require("./assets/logos/wechat-pay.png"),
        },
        {
          name: "Nets",
          checked: false,
          imgSrc: require("./assets/logos/nets.png"),
        },
        {
          name: "Nets Pay",
          checked: false,
          imgSrc: require("./assets/logos/nets-pay.png"),
        },

        {
          name: "Via",
          checked: false,
          imgSrc: require("./assets/logos/via.png"),
        },
        {
          name: "Grab Pay",
          checked: false,
          imgSrc: require("./assets/logos/grab-pay.png"),
        },
        {
          name: "Razer Pay",
          checked: false,
          imgSrc: require("./assets/logos/razer-pay.png"),
        },
        {
          name: "Remo Pay",
          checked: false,
          imgSrc: require("./assets/logos/remo-pay.png"),
        },
      ],
      extraOptions: [
        {
          name: "Bee Pay",
          checked: false,
          imgSrc: require("./assets/logos/bee-pay.png"),
        },
        {
          name: "DBS Paylah",
          checked: false,
          imgSrc: require("./assets/logos/dbs-paylah.png"),
        },
        {
          name: "OCBC Pay Anyone",
          checked: false,
          imgSrc: require("./assets/logos/ocbc-pay-anyone.png"),
        },

        {
          name: "Fave Pay",
          checked: false,
          imgSrc: require("./assets/logos/fave-pay.png"),
        },
        {
          name: "SCB",
          checked: false,
          imgSrc: require("./assets/logos/scb.png"),
        },
        {
          name: "UOB",
          checked: false,
          imgSrc: require("./assets/logos/uob.png"),
        },
      ],
    };
  },
  computed: {
    checkedOptionsCount() {
      return this.options.filter((option) => option.checked).length;
    },
    verticalOrientationIconDimensions() {
      if (this.selectedOptions.length <= 2) {
        return { "max-width": "30%" };
      } else if (this.selectedOptions.length <= 6) {
        return { "max-width": "23.5%" };
      } else if (this.selectedOptions.length <= 8) {
        return { "max-width": "20%" };
      } else if (this.selectedOptions.length <= 18) {
        return { "max-width": "14%" };
      } else {
        return { "max-width": "12%" };
      }
    },
    horizontalOrientationIconDimensions() {
      if (this.selectedOptions.length <= 2) {
        return { "max-width": "40%" };
      } else if (this.selectedOptions.length <= 4) {
        return { "max-width": "30%" };
      } else if (this.selectedOptions.length <= 6) {
        return { "max-width": "23.5%" };
      } else if (this.selectedOptions.length <= 8) {
        return { "max-width": "20%" };
      } else if (this.selectedOptions.length <= 15) {
        return { "max-width": "14%" };
      } else {
        return { "max-width": "12%" };
      }
    },
    sidesOrientationIconDimensions() {
      if (this.selectedOptions.length <= 4) {
        return { "max-width": "60%" };
      } else if (this.selectedOptions.length <= 10) {
        return { "max-width": "40%" };
      } else {
        return { "max-width": "25%" };
      }
    },
  },
  methods: {
    toggleOption(option) {
      if (this.selectedOptions.includes(option)) {
        this.selectedOptions = this.selectedOptions.filter(
          (item) => item != option
        );
        option.checked = false;
      } else {
        this.selectedOptions.push(option);
        option.checked = true;
      }
    },
    generatePDF() {
      let pdf;
      if (this.selectedOrientation == "horizontal") {
        pdf = new jsPDF("l", "mm", "a5");
      } else {
        pdf = new jsPDF("p", "mm", "a5");
      }

      var width = pdf.internal.pageSize.getWidth();
      var height = pdf.internal.pageSize.getHeight();
      html2canvas(document.getElementById("poster"), { scale: 3 }).then(
        function (canvas) {
          const imgData = canvas.toDataURL("image/png");
          pdf.addImage(imgData, "PNG", 0, 0, width, height);
          pdf.save("FOMO_Accepted_Here.pdf");
        }
      );
    },
  },
};
</script>

<style>
#app {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 60px;
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  overflow: hidden;
}

#left-panel {
  width: 50vw;
  padding: 5%;
  overflow: scroll;
}

#right-panel {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 50vw;
  padding: 5%;
  overflow-y: scroll;
}

.options-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.selectable {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  border-radius: 15px;
  padding: 10px;
  cursor: pointer;
}

.selectable:hover {
  background-color: rgb(255, 246, 246);
}

.selected {
  background-color: rgb(255, 217, 217);
}

.option-button {
  margin: 10px;
  width: 100px;
  height: 130px;
}

.generate-pdf-button {
  background-color: rgb(255, 74, 74);
  border-radius: 15px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
}

.tab-bar-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height: 50px;
  background-color: rgb(245, 245, 245);
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 50px;
}

.tab-bar-button {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
}

.tab-bar-button:hover {
  background-color: rgb(223, 223, 223);
}

.tab-bar-button-selected {
  background-color: rgb(187, 187, 187);
}

/* .red-frame {
  width: 80%;
  height: 90%;
  background: rgb(233, 16, 0);
  padding: 5% 10% 10% 10%;
  text-align: center;
} */

.accepted-here-text {
  font-size: 36px;
  color: white;
  font-weight: 700;
  margin-bottom: 10px;
}

.footer {
  background-color: #d12027;
  width: 90%;
  padding: 0% 5%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  font-weight: 700;
  color: white;
}

.footer-left {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-right {
  text-align: right;
}

.white-background {
  width: 100%;
  height: 90%;
  background: white;
  border-radius: 20px;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.logos-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.anchor-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.fomo-logo {
  width: 120px;
  height: auto;
}

.payment-logo {
  height: auto;
  margin: 5px;
}
</style>
